.errorMessage {
    font-size: 12px;
    letter-spacing: 0.05rem;
    padding-left: 0.25rem;
    color:red
  }
  .fullWidth{
      width:100%
  }
  .Gradient{
    color: white !important;
    background: -webkit-linear-gradient(rgb(29, 29, 170), rgb(20, 207, 231)); /* For Safari 5.1 to 6.0 */
    background: -o-linear-gradient(rgb(29, 29, 170), rgb(20, 207, 231)); /* For Opera 11.1 to 12.0 */
    background: -moz-linear-gradient(rgb(29, 29, 170), rgb(20, 207, 231)); /* For Firefox 3.6 to 15 */
    background: linear-gradient(rgb(29, 29, 170), rgb(20, 207, 231)); /* Standard syntax (must be last) */
  } 