.container{
    background-color: #cce5ff;
    background: -webkit-linear-gradient(rgb(20, 207, 231),#cce5ff); /* For Safari 5.1 to 6.0 */
    background: -o-linear-gradient(rgb(20, 207, 231),#cce5ff); /* For Opera 11.1 to 12.0 */
    background: -moz-linear-gradient(rgb(20, 207, 231),#cce5ff); /* For Firefox 3.6 to 15 */
    background: linear-gradient(rgb(20, 207, 231),#cce5ff );
    height: 100vh;
    max-width: 100vw !important;
}